<template>
	<div class="forgePass">
		<RXnav></RXnav>
		<div class="login-box" v-show="isShow">
			<!-- 登陆界面提示 -->
			<div class="login-title">
				<p class="gotitle">找回密码!</p>
				<p class="goEng">Find the password!</p>
			</div>
			<!-- 账号密码 -->
			<div class="login-textBox">
				<van-cell-group>
					<van-field v-model="userForgeInfo.Email" label="邮箱 :" placeholder="请输入邮箱地址" />
					<van-field v-model="userForgeInfo.Code" center clearable label="短信验证码" placeholder="请输入短信验证码">
						<template #button>
							<van-button @click="verification" size="small" type="primary">发送验证码</van-button>
						</template>
					</van-field>
				</van-cell-group>
				<div class="noPass" @click="logins">
					<span>已有账号,立即登录?</span>
				</div>
			</div>
			<!-- 登陆按钮 -->
			<div class="btns">
				<van-button color="#21b276" block round @click="nextStep">下一步</van-button>
			</div>
		</div>

		<!-- 验证成功后 -->
		<div class="login-box" v-show="!isShow">
			<!-- 登陆界面提示 -->
			<div class="login-title">
				<p class="gotitle">找回密码!</p>
				<p class="goEng">Find the password!</p>
			</div>
			<!-- 账号密码 -->
			<div class="login-textBox">
				<van-cell-group>
					<van-field v-model="userForgeInfo.phone" label="手机号 :" placeholder="请输入手机号码" />
					<van-field v-model="userForgeInfo.newPassWord" :type="isText ? 'text':'password' " label="密码 :" placeholder="请输入密码"
					:right-icon="isText?'eye-o':'closed-eye'" @click-right-icon="toggleType">
					</van-field>
				</van-cell-group>
				<div class="noPass" @click="logins">
					<span>已有账号,立即登录</span>
				</div>
			</div>
			<!-- 登陆按钮 -->
			<div class="btns">
				<van-button color="#21b276" block round @click="Submit">提交</van-button>
				<van-button class="last" color="#ccc" block round @click="lastStep">上一步</van-button>
			</div>
		</div>
	</div>
</template>

<script>
	import "../assets/less/ForgePass.less"
	import RXnav from "../components/RXnav.vue"
	import {
		utils
	} from "../assets/utils/utils.js"
	export default {
		name: "ForgePass",
		data() {
			return {
				// 用户邮箱信息
				userForgeInfo: {
					Email: '',
					Code: '',
					phone: "",
					newPassWord: ""
				},
				isShow: true,
				isText: false
			}
		},
		components: {
			RXnav
		},
		methods: {
			//立即登录
			logins(){
				this.$router.push({name:"Login"})
			},
			//获取验证码
			verification(){
				let obj = {
					Email: {
						value: this.userForgeInfo.Email,
						reg: /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,
						errorMsg: '邮箱地址不正确'
					},
				
				};
				if (!utils.validForm(obj)) {
					return;
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				
				});
				
				//请求方式
				this.axios({
					method: 'POST',
					url: '/emailValidCode',
					//如果是post请求,参数需要挂载到data
					data: {
						email: this.userForgeInfo.Email,
					}
				}).then(res => {
					//如果请求成功清掉提示
					this.$toast.clear();
					console.log(res);
					if (res.data.code == "J001") {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			//切换密码明文
			toggleType() {
				this.isText = !this.isText
			},
			//下一步
			nextStep() {
				// console.log(this.userForgeInfo.Email);
				if (this.userForgeInfo.Email==""||this.userForgeInfo.Code=="") {
					this.$toast.loading({
						message: '请输入邮箱地址或者验证码...',
						forbidClick: true,
						dutation: 0
					});
					return;
				}
				let obj = {
					Email: {
						value: this.userForgeInfo.Email,
						reg: /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,
						errorMsg: '邮箱地址不正确'
					},
				
				};
				if (!utils.validForm(obj)) {
					return;
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				
				});
				
				//请求方式
				this.axios({
					method: 'POST',
					url: '/checkValidCode',
					//如果是post请求,参数需要挂载到data
					data: {
						validCode: this.userForgeInfo.Code,
					}
				}).then(res => {
					//如果请求成功清掉提示
					this.$toast.clear();
					// console.log(res);
					if (res.data.code == "K001") {
						this.isShow = false
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			//提交更改密码
			Submit(){
				let obj = {
					phone: {
						value: this.userForgeInfo.phone,
						reg: /^1[3-9]\d{9}$/,
						errorMsg: '手机号格式不正确'
					},
					password: {
						value: this.userForgeInfo.newPassWord,
						reg: /^[A-Za-z]\w{5,15}$/,
						errorMsg: '密码支持字母数字_组合且首字符必须为字母'
					},
				
				};
				if (!utils.validForm(obj)) {
					return;
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				
				});
				
				//请求方式
				this.axios({
					method: 'POST',
					url: '/retrievePassword',
					//如果是post请求,参数需要挂载到data
					data: {
						phone: this.userForgeInfo.phone,
						password:this.userForgeInfo.newPassWord
					}
				}).then(res => {
					//如果请求成功清掉提示
					this.$toast.clear();
					// console.log(res);
					if (res.data.code == "L001") {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
						this.$router.push({name:"Login"})
					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			//返回上一步
			lastStep() {
				this.isShow = true
			}
		}
	}
</script>
